import excelToTable from "./excelToTable/index";
const components = [excelToTable];
//  install  Vue  use 
const install = (Vue) => {
  // 
  if (install.installed) return;
  // 
  components.map((component) => Vue.component(component.name, component));
};
// 
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}
export default {
  //  install Vue.use() 
  install,
  // 
  excelToTable,
};
